.contactsflex {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 10px;
}

.contactinput {
  width: 284px;
  height: 24px;
  border-radius: 5px;
  border: none;
  padding-left: 8px;
  padding-right: 8px;
}

.contactbutton {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 200px;
  height: 22px;
  border-radius: 5px;
  border: none;
  cursor: pointer;
}

.contactbutton:hover {
  background-color: rgba(128, 128, 128, 0.5);
}
